<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 搜索框和按钮 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            v-model="queryinfo.query"
            placeholder="请输入内容"
            class="input-with-select"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisable = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>
      <!-- 用户数据表格 -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label="姓名" width="180">
        </el-table-column>
        <el-table-column prop="email" label="邮箱"> </el-table-column>
        <el-table-column prop="mobile" label="电话"> </el-table-column>
        <el-table-column prop="role_name" label="角色"> </el-table-column>
        <el-table-column prop="mg_state" label="状态">
          <!-- 状态改变 -->
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="userstatechanged(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180px">
          <template slot-scope="scope">
            <!-- 修改按钮 -->
            <el-button
              v-model="scope.row"
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="changeUserinfo(scope.row.id)"
            >
            </el-button>
            <!-- 删除按钮 -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserbyId(scope.row.id)"
            ></el-button>
            <!-- 分配角色按钮 -->
            <el-tooltip
              effect="dark"
              content="分配角色"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRoles(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryinfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryinfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 添加用户的对话框 -->
    <el-dialog
      title="添加用户"
      :visible.sync="addDialogVisable"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体 -->

      <el-form
        :model="addFrom"
        :rules="rules"
        ref="addfromRef"
        label-width="70px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addFrom.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addFrom.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addFrom.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="addFrom.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部按钮 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisable = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改用户的对话框 -->
    <el-dialog
      title="修改用户"
      :visible.sync="changeDialogVisable"
      width="50%"
      @close="changeDialogClosed"
    >
      <!-- 内容主体 -->
      <el-form
        :model="chagneForm"
        :rules="rules"
        ref="changefromRef"
        label-width="70px"
      >
        <el-form-item label="用户名">
          <el-input v-model="chagneForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="chagneForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="chagneForm.mobile"></el-input>
        </el-form-item>
      </el-form>

      <!-- 底部按钮 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeDialogVisable = false">取 消</el-button>
        <el-button type="primary" @click="changeUser">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配角色的对话框 -->
    <el-dialog title="分配角色" :visible.sync="setRolesdialogVisible" width="50%" @close="setRolesdialogClosed">
      <div>
        <p>当前的用户：{{ userinfo.username }}</p>
        <p>当前的角色：{{ userinfo.role_name }}</p>
        <p>
          分配角色：
          <el-select v-model="selctedRoleId" placeholder="请选择">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRolesdialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //获取用户列表所需参数
      queryinfo: {
        query: "",
        pagenum: 1,
        pagesize: 2,
      },
      userlist: [],
      total: 0,
      //控制添加用户的对话框的显示和隐藏
      addDialogVisable: false,
      //控制修改用户的对话框的显示和隐藏
      changeDialogVisable: false,
      //控制分配角色的对话框的显示和隐藏
      setRolesdialogVisible: false,
      //添加用户的表单对象
      addFrom: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      //添加表单的验证规则对象
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 10 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 12,
            message: "长度在 6 到 12 个字符",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"],
          },
        ],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          {
            min: 11,
            max: 11,
            message: "长度为11个字符",
            trigger: "blur",
          },
        ],
      },
      //修改用户返回的用户信息
      chagneForm: [],
      //需要被分配角色的用户信息
      userinfo: {},
      //角色列表信息
      rolesList: [],
      //选中的角色ID值
      selctedRoleId: '',
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get("/users", {
        params: this.queryinfo,
      });
      if (res.meta.status !== 200) return this.$message.error("获取列表失败！");
      this.userlist = res.data.users;
      this.total = res.data.total;
      console.log(this.userlist)
    },
    //监听 页大小 改变事件
    handleSizeChange(newSize) {
      this.queryinfo.pagesize = newSize;
      this.getUserList();
    },
    //监听 页码值 改变事件
    handleCurrentChange(newPage) {
      this.queryinfo.pagenum = newPage;
      this.getUserList();
    },
    async userstatechanged(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      );
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state;
        return this.$message.error("更新状态失败!");
      }

      this.$message.success("更新状态成功！");
    },
    //关闭对话框的函数
    addDialogClosed() {
      this.$refs.addfromRef.resetFields();
    },
    changeDialogClosed() {
      this.$refs.changefromRef.resetFields();
    },
    addUser() {
      this.$refs.addfromRef.validate(async (valid) => {
        if (!valid) return;
        //发起网络请求添加用户
        const { data: res } = await this.$http.post("/users", this.addFrom);
        if (res.meta.status !== 201)
          return this.$message.error("添加用户失败！");
        this.$message.success("添加用户成功！");
        //隐藏添加用户对话框
        this.addDialogVisable = false;
        //重新获取列表
        this.getUserList();
      });
    },
    //修改前获取用户信息进行数据绑定显示
    async changeUserinfo(val) {
      this.changeDialogVisable = true;
      const { data: res } = await this.$http.get("/users/" + val);
      if (res.meta.status !== 200)
        return this.$message.error("用户信息请求失败！");
      this.chagneForm = res.data;
    },
    //修改用户信息
    changeUser() {
      this.$refs.changefromRef.validate(async (valid) => {
        if (!valid) return;
        //发起网络请求修改用户
        const { data: res } = await this.$http.put(
          "/users/" + this.chagneForm.id,
          { email: this.chagneForm.email, mobile: this.chagneForm.mobile }
        );
        if (res.meta.status !== 200)
          return this.$message.error("更新用户失败！");
        this.$message.success("更新用户成功！");
        //隐藏修改用户对话框
        this.changeDialogVisable = false;
        //重新获取列表
        this.getUserList();
      });
    },
    //根据ID来删除用户信息
    async removeUserbyId(id) {
      //弹框询问是否删除
      const re = await this.$confirm(
        "此操作将永久删除该用户, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      //用户取消删除 返回cancel
      if (re !== "confirm") return this.$message.info("已取消删除");
      const { data: res } = await this.$http.delete("/users/" + id);
      if (res.meta.status !== 200) return this.$message.error("删除失败!");
      this.$message.success("删除成功！");
      //重新获取列表
      this.getUserList();
    },
    //分配角色对话框
    async setRoles(roleinfo) {
      this.userinfo = roleinfo;
      //展示对话框之前显示所有的角色列表
      const { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200)
        return this.$message.error("获取角色列表失败!");
      this.$message.success("获取角色列表成功！");
      this.rolesList = res.data;
      this.setRolesdialogVisible = true;
    },
    //点击按钮分配角色
    async saveRoleInfo() {
      if (!this.selctedRoleId) {
        return this.$message.error("请选择需要分配的角色");
      }
      const { data: res } = await this.$http.put(
        
        `users/${this.userinfo.id}/role`,
        { rid: this.selctedRoleId }
      );
      if (res.meta.status !== 200) return this.$message.error("更新角色失败!");
      this.$message.success("更新角色成功！");
       
      this.setRolesdialogVisible = false;
     this.getUserList();
  },
    //分配角色的对话框关闭事件
    setRolesdialogClosed(){
      this.selctedRoleId =''
      this.userinfo ={}
    }
  },
};
</script>

<style lang="less" scoped>
</style>