<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col
          ><el-button type="primary" @click="AddRolesdialogVisible = true"
            >添加角色</el-button
          ></el-col
        >
      </el-row>
      <!-- 角色列表 -->
      <el-table :data="rolesList" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染第二和第三级权限 -->
              <el-col :span="19">
                <!-- 渲染第二级权限 -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <!-- 二级权限 -->
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 三级权限 -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editbackRoleinfo(scope.row.id)"
              >编辑</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delet"
              size="mini"
              @click="DeletRoleinfo(scope.row.id)"
              >删除</el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showdialogVisible(scope.row)"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加角色对话框 -->
    <el-dialog
      title="添加角色"
      :visible.sync="AddRolesdialogVisible"
      width="50%"
    >
      <!-- 内容主体 -->
      <el-form :model="AddForm" label-width="70px">
        <el-form-item label="角色名">
          <el-input v-model="AddForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述">
          <el-input v-model="AddForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="AddRolesdialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="AddRolesinfo">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑对话框 -->
    <el-dialog title="编辑" :visible.sync="EditRolesdialogVisible" width="50%">
      <el-form :model="AddForm" label-width="70px">
        <el-form-item label="角色名称">
          <el-input v-model="chagneForm.roleName" disabled></el-input>
        </el-form-item>
        <el-form-item label="角色描述">
          <el-input v-model="chagneForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditRolesdialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRoleinfo">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 分配权限对话框 -->
    <el-dialog
      title="分配权限"
      :visible.sync="FpdialogVisible"
      @close="clearKeys"
      width="50%"
    >
      <el-tree
        :data="rightList"
        :props="treeprops"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defkeys"
        ref="treeRef"
      >
      </el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="FpdialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRight">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //角色列表
      rolesList: [],
      //定义分配权限对话框的状态
      FpdialogVisible: false,
      //定义添加角色的对话框状态
      AddRolesdialogVisible: false,
      //定义编辑角色的对话框状态
      EditRolesdialogVisible: false,
      //所有权限数据
      rightList: [],
      //树形控件绑定的对象
      treeprops: {
        children: "children",
        label: "authName",
      },
      //默认选中节点的ID值的数组
      defkeys: [],
      //分配权限的角色ID
      roleId: "",
      //添加角色表单信息
      AddForm: {
        roleName: "",
        roleDesc: "",
      },
      chagneForm: [],
    };
  },
  created() {
    //获取权限函数
    this.getrolesList();
  },
  methods: {
    async getrolesList() {
      const { data: res } = await this.$http.get("/roles");
      if (res.meta.status !== 200)
        return this.$message.error("获取角色列表失败!");
      this.rolesList = res.data;
    },
    async removeRightById(role, right) {
      const confirmResult = await this.$confirm(
        "此操作将永久删除该权限, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);

      if (confirmResult !== "confirm") return this.$message.info("取消了删除");

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${right}`
      );
      if (res.meta.status !== 200) return this.$message.error("删除失败！");
      role.children = res.data;
    },
    async showdialogVisible(Role) {
      this.roleId = Role.id;
      //获取数据
      const { data: res } = await this.$http.get("rights/tree");

      if (res.meta.status !== 200) {
        return this.$message.error("获取权限数据失败");
      }
      this.rightList = res.data;
      //调用函数获取所有三级节点
      this.getRefKeys(Role, this.defkeys);

      this.FpdialogVisible = true;
    },

    //获取第三极权限 绑定到数组Refkeys当中
    getRefKeys(node, arr) {
      //如果不含子节点就是第三级节点
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => {
        this.getRefKeys(item, arr);
      });
    },
    clearKeys() {
      this.defkeys = [];
    },
    //给角色分配权限
    async allotRight() {
      const key = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
      const idStr = key.join(",");
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      );
      if (res.meta.status !== 200) return this.$message.error("分配权限失败！");
      this.$message.success("分配权限成功！");

      this.getrolesList();
      this.FpdialogVisible = false;
    },
    //添加角色
    async AddRolesinfo() {
      const { data: res } = await this.$http.post("roles", this.AddForm);
      if (res.meta.status !== 201) return this.$message.error("添加角色失败！");
      this.$message.success("添加角色成功！");
      this.getrolesList();
      this.AddRolesdialogVisible = false;
    },
    //点击编辑显示返回的信息绑定
    async editbackRoleinfo(id) {
      this.EditRolesdialogVisible = true;
      const { data: res } = await this.$http.get("/roles/" + id);
      if (res.meta.status !== 200)
        return this.$message.error("用户信息请求失败！");
      this.chagneForm = res.data;
    },
    //点击编辑对话框的确认 编辑角色信息
    async editRoleinfo() {
      const { data: res } = await this.$http.put(
        `roles/${this.chagneForm.roleId}/`,
        {
          roleName: this.chagneForm.roleName,
          roleDesc: this.chagneForm.roleDesc,
        }
      );
      if (res.meta.status !== 200) return this.$message.error("更新角色失败！");
      this.$message.success("更新角色成功！");

      //隐藏对话框
      this.EditRolesdialogVisible = false;
      //重新获取角色信息
      this.getrolesList();
    },
    //删除角色信息
    async DeletRoleinfo(id) {
      const re = await this.$confirm(
        "此操作将永久删除该角色, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      //用户取消删除 返回cancel
      if (re !== "confirm") return this.$message.info("已取消删除");
      const { data: res } = await this.$http.delete("/roles/" + id);
      if (res.meta.status !== 200) return this.$message.error("删除失败!");
      this.$message.success("删除成功！");
      //重新获取角色信息
      this.getrolesList();
    },
  },
};
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>