<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt="" />
        <span>后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <!-- 页面主要区域 -->
    <el-container>
      <!-- 左侧边栏 -->
      <el-aside :width="isColl ? '64px' : '200px'">
          <!-- 侧边栏菜单 -->
          <div class="toggle-button" @click="togg">
            |||
          </div>
        <el-menu
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          unique-opened
          :collapse="isColl"
          :collapse-transition="false"
          :router="true"
          :default-active="activePate"
        >
        <!-- 一级菜单 -->
          <el-submenu :index="(item.id)+''" v-for="item in MenuList" :key="item.id">
              <!-- 一级菜单模板区域 -->
            <template slot="title">
                <!-- i 图标 -->
              <i :class="iconsObj[item.id]"></i>
                <!-- span文本 -->
              <span>{{item.authName}}</span>
            </template>

            <!-- 二级菜单 -->
            <el-menu-item :index="'/'+(item2.path)" v-for="item2 in item.children" :key="item2.id"
              @click="saveNavstate(index='/'+item2.path)"
            >
                
              <!-- 二级菜单模板区域 -->
            <template slot="title">
                <!-- i 图标 -->
              <i class="el-icon-menu"></i>
                <!-- span文本 -->
              <span>{{item2.authName}}</span>
            </template>

            </el-menu-item>
          </el-submenu>
        
        </el-menu>
      </el-aside>
      <!-- 右边主要内容 -->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data(){
    return{
      MenuList:[],
      iconsObj:{
        "125":"el-icon-s-custom",
        '103':'el-icon-unlock',
        '101':'el-icon-s-grid',
        '102':'el-icon-s-order',
        '145':'el-icon-s-data'

      },
      //是否折叠
      isColl:false,
      //被激活的连接地址
      activePate:'',

    }
  },
  created(){
    this.getMenuList()
    this.activePate = window.sessionStorage.getItem('active')
  },
  methods: {

    logout() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    async getMenuList(){
      const {data:res} = await this.$http.get('menus')
      if(res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.MenuList = res.data
    },
    togg(){
      this.isColl = !this.isColl
    },
    //保存激活的状态
    saveNavstate(val){
      window.sessionStorage.setItem('active',val)
      this.activePate = val
    }
  },
};
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}
.el-header {
  background-color: rgb(120, 139, 134);
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
  img {
    width: 70px;
    height: 70px;
  }
}
.el-aside {
  background-color: rgb(84,92,100);
  .el-menu{
    border-right: none;
  }
}
.el-main {
  background-color: rgb(228, 222, 226);
}
.toggle-button{
  background-color: rgb(70, 73, 75);
  font-size: 15px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>