<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 提示区 -->
      <el-alert
        title="添加商品信息"
        type="info"
        center
        show-icon
        :closable="false"
      >
      </el-alert>
      <!-- 步骤条 -->
      <el-steps
        :space="200"
        :active="activeindex - 0"
        finish-status="success"
        align-center
      >
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- tab栏 -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeindex"
          :tab-position="'left'"
          :before-leave="beforeleave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                expand-trigger="hover"
                :options="catelist"
                :props="cateProps"
                v-model="addForm.goods_cat"
                @change="handlechange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="商品参数" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTabledata"
              :key="item.attr_id"
            >
              <!-- 复选框组 -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  border
                  :label="cb"
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="商品属性" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- action表示上传到的后台api地址，on-preview预览窗口，remove删除 -->
            <el-upload
              :headers="headerObj"
              action="https://www.liulongbin.top:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">
                只能上传jpg/png文件，且不超过500kb
              </div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器 -->
            <quill-editor v-model="addForm.good_introduce">

            </quill-editor>
            <el-button type="primary" style="margin-top:10px" @click="add">添加商品</el-button>

          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- //图片预览的对话框 -->
    <el-dialog
      title="图片预览"
      :visible.sync="previewdialogVisible"
      width="50%"
    >
      <img :src = "previewPath" alt="" class="imgpreview">
     
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data() {
    return {
      activeindex: "0",
      //验证规则
      addFormRules: {
        goods_name: [
          { required: true, message: "请输入商品名称", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "请输入商品价格", trigger: "blur" },
        ],
        goods_weight: [
          { required: true, message: "请输入商品重量", trigger: "blur" },
        ],
        goods_number: [
          { required: true, message: "请输入商品数量", trigger: "blur" },
        ],
        goods_cat: [
          { required: true, message: "请选择商品分类", trigger: "blur" },
        ],
      },
      //输入的商品信息
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        //商品所属的分类数组
        goods_cat: [],
        //上传成功的图片数组
        pics: [],
        //文本描述
        good_introduce:'',
        //参数和属性
        attrs:[]
      },
      //商品分类列表
      catelist: [],
      //配置对象
      cateProps: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
      },
      //动态参数列表数据
      manyTabledata: [],
      //静态属性列表数据
      onlyTableData: [],
      //图片上传由于E UI自动封装的请求操作没有通过axios拦截器附带token 需要自己附带请求头
      headerObj: {
        Authorization: window.sessionStorage.getItem("token"),
      },
      //图片预览时的路径
      previewPath: "",
      //图片对话框初始设置
      previewdialogVisible:false,

    };
  },
  created() {
    this.getCateList();
  },
  computed: {
    getcateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2];
      }
      return null;
    },
  },
  methods: {
    //获取商品分类数据
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) return this.$message.error("获取失败！");
      this.catelist = res.data;
    },
    //级联选择器选中项变化
    handlechange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = [];
      }
    },
    //离开标签页的变化
    beforeleave(activeName, oldactiveName) {
      if (oldactiveName === "0" && this.addForm.goods_cat.length !== 3) {
        this.$message.error("请先选择商品分类信息");
        return false;
      }
    },
    //tab选找时
    async tabClicked() {
      //如果是1 则访问的是动态参数面板
      if (this.activeindex === "1") {
        const { data: res } = await this.$http.get(
          `categories/${this.getcateId}/attributes`,
          { params: { sel: "many" } }
        );
        if (res.meta.status !== 200)
          return this.$message.error("获取参数失败！");

        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(" ");
        });
        this.manyTabledata = res.data;
      } else if (this.activeindex == "2") {
        const { data: res } = await this.$http.get(
          `categories/${this.getcateId}/attributes`,
          { params: { sel: "only" } }
        );
        if (res.meta.status !== 200)
          return this.$message.error("获取属性失败！");
       
        this.onlyTableData = res.data;
      }
    },
    //图片预览的钩子函数
    handlePreview(file) {
      this.previewPath = file.response.data.url;
      this.previewdialogVisible = true
    },
    //删除图片的钩子函数
    handleRemove(file) {
      //要删除图片的临时路径
      const filePath = file.response.data.tmp_path;
      //从数组中根据路径找到这个图片的索引值
      const i = this.addForm.pics.findIndex((x) => x.pic === filePath);
      //根据索引删除
      this.addForm.pics.splice(i, 1);
      
    },
    //上传成功的钩子函数
    handleSuccess(response) {
      //得到图片信息的对象
      const picINfo = { pic: response.data.tmp_path };
      //push进数组
      this.addForm.pics.push(picINfo);
      
    },
    //加商品按钮
    add(){
      //验证表单
      this.$refs.addFormRef.validate(async valid =>{
        if(!valid){
          return this.$message.error('请填写必要的表单项！')
        }
        //执行添加
        //首先将good_cat数组转换成字符串
        //深拷贝 ladash包的 cloneDeep（obj）
       const form =  _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        //处理动态参数和静态属性
        this.manyTabledata.forEach(item => {
          const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals.join(' ')}
          this.addForm.attrs.push(newInfo)
        })
        this.onlyTableData.forEach(item =>{
          const newInfo = {attr_id:item.attr_id,attr_value:item.attr_vals}
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)
        //发起请求添加商品

        const {data:res} = await this.$http.post('goods',form)
        if(res.meta.status !== 201) 
            return this.$message.error("添加商品失败！");
           this.$message.success("添加商品成功！");
           this.$router.push('/goods')
      })
    },
  },
};
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.imgpreview{
  width: 100%;
}
</style>