<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索框 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            v-model="paperinfo.query"
            placeholder="请输入内容"
            clearable
            @clear="getpaperList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getpaperList"
            ></el-button> </el-input
        ></el-col>
      </el-row>
      <!-- 商品列表数据表格 -->
      <el-table :data="paperList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="订单编号" prop="order_number"></el-table-column>
        <el-table-column
          label="订单价格(元)"
          prop="order_price"
          width="120px"
        ></el-table-column>
        <el-table-column label="是否付款" prop="pay_status" width="90px">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"
              >已付款</el-tag
            >
            <el-tag type="danger" v-else>未付款</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label="是否发货"
          prop="is_send"
          width="80px"
        ></el-table-column>
        <el-table-column label="下单时间" prop="create_time" width="160px">
          <template slot-scope="scope">
            {{ scope.row.create_time | dataFormat }}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="220px">
          <template slot-scope="">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="showBox"
              >编辑</el-button
            >
            <el-button type="success" size="mini" icon="el-icon-location" @click="showprogressBox"
              >查看定位</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="paperinfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="paperinfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
    <!-- //修改信息对话框 -->
    <el-dialog title="修改地址" :visible.sync="addressVisiable" width="50%" @close = "addressDialogClosed">
      <el-form
        :model="addressForm"
        :rules="rules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item label="省市区/县" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1">

          </el-cascader>
        </el-form-item>
          <el-form-item label="详细地址" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisiable = false,addressDialogClosed">取 消</el-button>
        <el-button type="primary" @click="addressVisiable = false,addressDialogClosed"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <!-- //查看物流进度的对话框 -->
    <el-dialog
  title="物流进度"
  :visible.sync="progressdialogVisible"
  width="50%"
  >
   <el-timeline >
    <el-timeline-item
      v-for="(activity, index) in progressInfo"
      :key="index"
      :timestamp="activity.time">
      {{activity.context}}
    </el-timeline-item>
  </el-timeline>
  
</el-dialog>
  </div>
</template>

<script>
import cityData from '@/js/citydata.js'

export default {
  data() {
    return {
      //查询订单参数
      paperinfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      //订单列表
      paperList: [],
      //总数据条数
      total: 0,
      //对话框初始设置
      addressVisiable: false,
      progressdialogVisible:false,
      //地址表单
      addressForm:{
          address1:[],
          address2:'',
      },
      //规则
      rules:{
          address1:[{required: true, message: '请选择省市区/县', trigger: 'blur'}],
         address2:[{required: true, message: '请输入详细地址', trigger: 'blur'}]
      
      },
      //城市数据
      cityData,
      //物流信息
      progressInfo:[],

    };
  },
  created() {
    this.getpaperList();
  },
  methods: {
    //根据分页获取商品列表
    async getpaperList() {
      const { data: res } = await this.$http.get("orders", {
        params: this.paperinfo,
      });
      
      if (res.meta.status !== 200) {
        return this.$message.error("获取订单列表失败！");
      }
      this.$message.success("获取订单列表成功！");
      this.paperList = res.data.goods;
      this.total = res.data.total;
      
    },
    //监听pagesizi的改变
    handleSizeChange(newSize) {
      this.paperinfo.pagesize = newSize;
      this.getpaperList();
    },
    //监听pagenum的改变
    handleCurrentChange(newNum) {
      this.paperinfo.pagenum = newNum;
      this.getpaperList();
    },
    //展示修改地址对话框
    showBox() {
        this.addressVisiable = true
    },
    //关闭清空
    addressDialogClosed(){
        this.$refs.addFormRef.resetFields()
    },

   async showprogressBox(){
       const {data:res} = await this.$http.get('/kuaidi/:id')
         if (res.meta.status !== 200) {
        return this.$message.error("获取物流信息失败！");
      }
      this.$message.success("获取物流信息成功！");
      this.progressInfo = res.data
     this.progressdialogVisible = true
    }
  },
};
</script>

<style lang="less" scoped>
.el-cascader{
    width: 100%;
}
</style>