<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert
        :closable="false"
        title="注意：只允许为第三级分类设置相关参数"
        type="warning"
        show-icon
      >
      </el-alert>
      <el-row class="elrow">
        <el-col>
          <span>选择放商品分类：</span>
          <el-cascader
            expand-trigger="hover"
            v-model="selectedcateKeys"
            :options="catelist"
            :props="cateProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tab页签 -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 动态参数面板 -->
        <el-tab-pane label="动态参数" name="many">
          <el-button
            type="primary"
            :disabled="isBtn"
            @click="addDialogVisiable = true"
            >添加参数</el-button
          >
          <!-- 动态参数表格 -->
          <el-table :data="manyTableDate" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand"></el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label="参数名称"
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  >编辑</el-button
                >
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="deletParams(scope.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- 静态属性面板 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button
            type="primary"
            :disabled="isBtn"
            @click="addDialogVisiable = true"
            >添加属性</el-button
          >
          <!-- 静态属性表格 -->
          <el-table :data="onlyTableDate" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand"></el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label="属性名称"
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  >编辑</el-button
                >
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="deletParams(scope.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 添加数据的公用对话框 -->
    <el-dialog
      :title="'添加' + titleText"
      :visible.sync="addDialogVisiable"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        ref="addFormRef"
        :model="addForm"
        label-width="80px"
        :rules="rule"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisiable = false">取 消</el-button>
        <el-button type="primary" @click="addparams">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改数据的公用对话框 -->
    <el-dialog
      :title="'修改' + titleText"
      :visible.sync="editDialogVisiable"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :model="editForm"
        label-width="80px"
        :rules="rule"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisiable = false">取 消</el-button>
        <el-button type="primary" @click="editparams">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      catelist: [],
      // 级联选择框对象
      cateProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      //级联选择框双向绑定数据
      selectedcateKeys: [],
      //激活的页签
      activeName: "many",
      //动态参数数据
      manyTableDate: [],
      //静态属性数据
      onlyTableDate: [],
      //添加对话框的初始设置
      addDialogVisiable: false,
      //修改对话框的初始设置
      editDialogVisiable: false,
      //添加参数的表单对象
      addForm: {},
      //修改参数的表单对象
      editForm: {},

      //验证表单的对象
      rule: {
        attr_name: [
          { required: true, message: "请输入数据名称", trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    isBtn() {
      if (this.selectedcateKeys.length !== 3) {
        return true;
      }
      return false;
    },
    //获取三级分类的ID
    cateID() {
      if (this.selectedcateKeys.length === 3) {
        return this.selectedcateKeys[2];
      }
      return null;
    },
    //动态获取激活的面板的按钮标题
    titleText() {
      if (this.activeName === "many") return "动态参数";
      return "静态属性";
    },
  },
  created() {
    //获取所有的商品分类列表
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        return this.$message.error("获取失败！");
      }
      this.catelist = res.data;
    },
    //级联选择框选中项改变
    handleChange() {
      this.getParmasDate();
    },
    //tab页签点击事件
    handleClick() {
      this.getParmasDate();
    },
    //获取参数列表
    async getParmasDate() {
      //如果不是三级分类
      if (this.selectedcateKeys.length !== 3) {
        this.selectedcateKeys = [];
        this.manyTableDate = [];
        this.onlyTableDate = [];
        return;
      }
      //只能选择三级分类
      const { data: res } = await this.$http.get(
        `categories/${this.cateID}/attributes`,
        { params: { sel: this.activeName } }
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("获取参数列表失败！");
      }
      if (this.activeName === "many") this.manyTableDate = res.data;
      else this.onlyTableDate = res.data;
    },
    //监听对话框的关闭事件
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    //预校验通过后发起请求
    addparams() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          `categories/${this.cateID}/attributes`,
          { attr_name: this.addForm.attr_name, attr_sel: this.activeName }
        );
        if (res.meta.status !== 201) return this.$message.error("创建失败！");
        this.$message.success("创建成功！");
        this.addDialogVisiable = false;
        //重新获取参数列表
        this.getParmasDate();
      });
    },
    //点击按钮显示修改的对话框
    async showEditDialog(attr_id) {
      const { data: res } = await this.$http.get(
        `categories/${this.cateID}/attributes/${attr_id}`,
        { params: { attr_sel: this.activeName } }
      );
      if (res.meta.status !== 200) return this.$message.error("获取失败!");
      this.editForm = res.data;
      this.editDialogVisiable = true;
    },
    //重置修改的表单
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //点击按钮修改参数信息
    async editparams() {
      const { data: res } = await this.$http.put(
        `categories/${this.cateID}/attributes/${this.editForm.attr_id}`,
        { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
      );
      if (res.meta.status !== 200) return this.$message.error("修改参数失败！");
      this.$message.success("修改参数成功！");
      this.getParmasDate();
      this.editDialogVisiable = false;
    },
    //点击删除按钮删除参数
    async deletParams(attr_id) {
      //弹框询问是否删除
      const re = await this.$confirm(
        "此操作将永久删除该分类, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      //用户取消删除 返回cancel
      if (re !== "confirm") return this.$message.info("已取消删除");

      const { data: res } = await this.$http.delete(
        `categories/${this.cateID}/attributes/${attr_id}`
      );
      if (res.meta.status !== 200) return this.$message.error("删除失败！");
      this.$message.success("删除成功！");
      this.getParmasDate();
    },
  },
};
</script>

<style lang="less" scoped>
.elrow {
  margin: 15px 0;
}
</style>