<template>
  <div class="login_container">
    <img class="bg" src="../assets/dl.jpg" alt="" />
    <div class="login">
      <div class="login_box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!-- 表单登陆 -->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        label-width="0px"
        class="form_login"
      >
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            prefix-icon="el-icon-user"
          ></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password"> 
          <el-input
            v-model="loginForm.password"
            prefix-icon="el-icon-key"
            type="password"
          ></el-input>
        </el-form-item>
        <!-- 按钮 -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login">登陆</el-button>
          <el-button type="info" @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginForm: {
        username: "admin",
        password: "123456",
      },
      //表单验证规则
      loginFormRules: {
        //验证用户名合法
        username: [
          { required: true, message: "请输入登陆用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 10 个字符",
            trigger: "blur",
          },
        ],
        //验证密码是否合法
        password: [{ required: true, message: "请输入登陆密码", trigger: "blur" },
          {
            min: 6,
            max: 10,
            message: "长度在 6 到 10 个字符",
            trigger: "blur",
          },
        ]
      }

    };
  },
  methods:{
      reset(){
          this.$refs.loginFormRef.resetFields();
      },
      login(){
          this.$refs.loginFormRef.validate(async(valid)=>{
              if(!valid) return;
              const {data:res} = await this.$http.post('/login',this.loginForm);
              if(res.meta.status !== 200) return this.$message.error("登陆失败!");
              this.$message.success("登陆成功!");
              console.log(res)
              window.sessionStorage.setItem("token",res.data.token);
              this.$router.push('/home')
          });
      }
  }
};
</script>

.<style lang="less" scoped>
.login_container {
  .bg {
    width: 100%;
   height: 100%;
  }
  height: 100%;
  .login {
    width: 600px;
    height: 400px;
    background:transparent;
    color:#fff;
    background-color:rgba(255,255,255,0.75);
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .login_box {
      height: 130px;
      width: 130px;
      border: 1px solid #eee;
      border-radius: 50%;
      padding: 10px;
      box-shadow: 0 0 10px #ddd;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #eee;
      }
    }
  }
}
.btns {
  display: flex;
  justify-content: flex-end;
}
.el-button {
  font-size: 18px;
    margin: 10px 5px;
}
.el-input{
    font-size: 22px;
    width: 100%;
    height: 40px;
    margin: 5px 0;

}
.form_login {
  position: absolute;
  top: 37%;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
</style>