<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <el-button type="primary" @click="showAddcategoryVisiable()"
        >添加用户</el-button
      >
      <!-- 用户数据表格 -->
      <tree-table class="tree-table"
        :data="Catelist"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
      <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" style="color:lightgreen;" v-if="scope.row.cat_deleted === false"></i>
          <i class="el-icon-error" style="color:red;" v-else></i>
      </template>
           
        <template slot="level" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0">一级</el-tag>
            <el-tag v-else-if="scope.row.cat_level === 1" type="success">二级</el-tag>
            <el-tag v-else type="warning">三级</el-tag>
      </template>
      
         <template slot="opert" slot-scope="scope">
             <el-button type="primary" icon="el-icon-edit" size="mini" @click="findIdCateinfo(scope.row.cat_id)">修改</el-button>
             <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeIdCateinfo(scope.row.cat_id)">删除</el-button>
      </template>

      </tree-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="CateListinfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="CateListinfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 添加分类对话框 -->
    <el-dialog
  title="添加分类"
  :visible.sync="AddcategoryVisiable"
  width="30%"
  @close="addCateDiglogClose"
  >
  <el-form :model="addCateFrom" :rules="rules" ref="addCateFromRef" label-width="100px">
  <el-form-item label="分类名称:" prop="cat_name">
    <el-input v-model="addCateFrom.cat_name"></el-input>
  </el-form-item>
   <el-form-item label="父级分类:">
    <!-- options用于指定数据源 -->
    <!-- props用于指定配置对象 -->
  <el-cascader
    expand-trigger="hover"
    v-model="selectedKeys"
    :options="parentCatellist"
    :props="cascaderprops"
    clearable
    change-on-select
    @change="parentCatechange"></el-cascader>
    </el-form-item>
  </el-form>

  <span slot="footer" class="dialog-footer">
    <el-button @click="AddcategoryVisiable = false">取 消</el-button>
    <el-button type="primary" @click="addcate">确 定</el-button>
  </span>
</el-dialog>
    <!-- 修改对话框 -->
    <el-dialog
  title="编辑"
  :visible.sync="editCatedialogVisible"
  width="50%"
  >
  <el-form :model="changeCateFrom" :rules="rules" label-width="100px">
  <el-form-item label="分类名称:">
    <el-input v-model="changeCateFrom.cat_name"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editCatedialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="Editcate">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Catelist: [],
      //查询条件
      CateListinfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      //添加分类的表单数据
      addCateFrom:{
          //需要添加的分类名称
          cat_name:'',
          //父级ID
          cat_pid:0,
          //默认添加的分配的等级是0
          cat_level:0
      },
      total: 0,
      //添加分配对话框初始设置
      AddcategoryVisiable:false,
      //编辑对话框的设置
      editCatedialogVisible:false,
      //Table列的定义
      columns: [
        {
          label: "分类名称",
          prop: "cat_name",
        },
        {   
            //定义为模板列 
            label:"是否有效",
            type:"template",
            template:'isok'
        },
        {   
            //定义为模板列2 
            label:"排序",
            type:"template",
            template:'level'
        },
         {   
            //定义为模板列2 
            label:"操作",
            type:"template",
            template:'opert'
        },
      ],
      //添加分类表单验证
      rules:{
        cat_name:[
             { required: true, message: '请输入分类名称', trigger: 'blur' },
        ]
      },
      //父级分类的列表
      parentCatellist:[],
      //指定级联选择器配置对象
      cascaderprops:{
          value:'cat_id',
          label:'cat_name',
          children:'children'
      },
      //选中的父级分类的ID数组
      selectedKeys:[],
      //编辑按钮返回的分类信息
      changeCateFrom:{},
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: this.CateListinfo,
      });
      if (res.meta.status !== 200) return this.$message.error("获取失败!");
      this.Catelist = res.data.result;
      this.total = res.data.total;
    },
    addDialogClosed() {
      this.AddcategoryVisiable = ture;
    },
    //监听pagesize改变事件
    handleSizeChange(newSize) {
        this.CateListinfo.pagesize = newSize
        this.getCateList()
    },
    //监听pagenum页码改变事件
    handleCurrentChange(newNum) {
        this.CateListinfo.pagenum = newNum
        this.getCateList()
    },
    showAddcategoryVisiable(){
        //打开对话框前获取数据
        this.getParentCateList()
        this.AddcategoryVisiable = true
    },
    //获取父级分配的数据
  async  getParentCateList(){
      const {data:res} = await  this.$http.get('categories',{params:{
            type:2
        }})
        if(res.meta.status !== 200){
            return this.$message.error('获取父级数据失败')
        }
        this.parentCatellist = res.data
        
    },
    //选择项变化
    parentCatechange(){
        //根据selectedKeys数组的长度来判断是否选中了父级分类
        if(this.selectedKeys.length > 0){
            this.addCateFrom.cat_pid = this.selectedKeys[
                this.selectedKeys.length -1
            ]
        //等级赋值
        this.addCateFrom.cat_level = this.selectedKeys.length
        return
        }
        else{
            //没选择 自为最高级父元素
            this.addCateFrom.cat_pid = 0
            this.addCateFrom.cat_level = 0
        }
       
    },
    //点击添加新的分类
    addcate(){
        this.$refs.addCateFromRef.validate(async valid =>{
            if(!valid) return
          const {data:res} = await this.$http.post('categories',this.addCateFrom)
          if(res.meta.status !== 201) return this.$message.error('添加分类失败！')
         this.$message.success('添加分类成功！')
         //刷新列表
          this.getCateList()
          //隐藏对话框
          this.AddcategoryVisiable = false
        })
        
    },
    //关闭对话框 重置表单
    addCateDiglogClose(){
        this.$refs.addCateFromRef.resetFields()
        this.selectedKeys = []
        this.addCateFrom.cat_level = 0
        this.addCateFrom.cat_pid = 0
   },
     //根据ID查询数据显示
 async findIdCateinfo(id){
    
    const {data:res} = await this.$http.get('categories/'+ id)
    if(res.meta.status !== 200) return this.$message.error('获取信息失败!')
    this.changeCateFrom = res.data
    console.log(this.changeCateFrom)
    this.editCatedialogVisible = true
 },
 //编辑返回的信息并提交
   async Editcate(){
    const {data:res} = await this.$http.put('categories/'+ this.changeCateFrom.cat_id,{cat_name:this.changeCateFrom.cat_name})
     if(res.meta.status !== 200) return this.$message.error('更新失败！')
     this.$message.success('更新成功！')
     this.getCateList()
     this.editCatedialogVisible = false
 },
  //根据ID来删除用户信息
    async removeIdCateinfo(id) {
      //弹框询问是否删除
      const re = await this.$confirm(
        "此操作将永久删除该分类, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      //用户取消删除 返回cancel
      if (re !== "confirm") return this.$message.info("已取消删除");
      const { data: res } = await this.$http.delete("categories/" + id);
      if (res.meta.status !== 200) return this.$message.error("删除失败!");
      this.$message.success("删除成功！");
      //重新获取列表
      this.getCateList();
    },
  },

  };
</script>

<style lang="less" scoped>
.tree-table{
    margin-top: 15px;
}
.el-cascader{
    width: 100%;
}
</style>